import React from "react";
import PropTypes from "prop-types";
import styles from "./styles.scss";
import Ionicon from "react-ionicons";
const Tags = (props,context)=>{
    return(
        <main className={styles.main}>
            {!props.seltag.includes(props.tag) &&  
                <button className={styles.buttonWhite} onClick={props.handleClick(props.tag)}> 
                    {props.tag} 
                </button>
            }
            {props.seltag.includes(props.tag) &&  
                <button className={styles.buttonBlack} onClick={props.handleClick(props.tag)}> 
                    {props.tag} 
                </button>
            }
        </main>
    );
}

Tags.propTypes={
    handleClick: PropTypes.func.isRequired,
    allreadyClick : PropTypes.func.isRequired,
    tag: PropTypes.string.isRequired,
}
export default Tags;


