import React, {Component} from "react";
import Music from "./presenter";
import PropTypes from "prop-types";

class Container extends Component {
    static propTypes = {
        getTagMusic: PropTypes.func.isRequired,
        pushTag: PropTypes.func.isRequired,
        popTag: PropTypes.func.isRequired
    };
    
    
    componentWillReceiveState = nextProps => {
        if (nextProps.melonMusic) {
            this.setState({
                loading: false
            });
        }
        
    };
    render() {
        return <Music handleClick={this.handleClick} {...this.state} {...this.props}/>;
    }
}

export default Container;


